body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-wrapper {
  width: 1440px;
  margin: 0 auto;
}

.App {
  height: "100vh";
  display: "flex";
  flex-direction: "column";
  justify-content: "space-between";
}

* {
  scrollbar-color: #000 #2B2B2B;
  scrollbar-width: thin;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #2B2B2B;
}

::-webkit-scrollbar-thumb {
  background-color: #000;
  border-radius: 9em;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #4e4e4e;
}

/* Стрелки */

::-webkit-scrollbar-button:vertical:start:decrement {
  display: none;
}

::-webkit-scrollbar-button:vertical:end:increment {
  display: none;
}

.topbar {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.cart-icon-wrapper {
  position: absolute;
  right: 0;
  margin-right: 125px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.cart-products-counter {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: black;
  margin-left: 8px;
  color: white;
  text-align: center;
}

.cart-wrapper {
  height: 100vh;
  width: 570px;
  background: #2B2B2B;
  position: fixed;
  right: 0;
  top: 0;
  text-align: left;
}

.cart {
  height: 100%;
  width: 340px;
  position: relative;
}

.cart-close {
  background: center url(/test_shop-page/static/media/cross.797223ce.svg);
  width: 20px;
  height: 20px;
  position: absolute;
  right: 0;
  margin: -101px 34px;
}

.items-list {
  margin-top: 143px;
  border-top: 1px solid #424242;
  max-height: 500px;
  overflow-y: auto;
}

.item {
  border-bottom: 1px solid #424242;
  padding: 19px 31px 19px 39px;
  display: flex;
}

.item-miniature {
  width: 83px;
  height: 83px;
  background-color: green;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.item-information {
  height: 56px;
  padding-top: 27px;
}

.cart-item-name {
  font-family: Roboto;
  font-style: normal;
  font-weight: 200;
  font-size: 12px;
  line-height: 14px;
  color: #FFFFFF;
  margin-left: 17px;
}

.counter-wrapper {
  color: white;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.7px;
  margin: 6px 15px;
  display: inline-block;
}

.counter-btn {
  background-color: transparent;
  border: none;
  color: white;
}

.price {
  display: inline-block;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 1px;
  color: #FFFFFF;
  margin-left: 56px;
}

.total {
  display: flex;
  justify-content: space-between;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.8px;
  color: #FFFFFF;
}

.total-element {
  padding: 40px 31px 0 40px;
}

.buy-btn {
  position: absolute;
  bottom: 183px;
  left: 40px;
  border: 2px solid #FFFFFF;
  box-sizing: border-box;
  background-color: transparent;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 22px 93px;
  cursor: pointer;
}

.buy-btn:focus {
  outline: none;
}

.empty-cart-text {
  text-align: center;
  height: 20px;
}
.main-page {
  display: flex;
  margin-top: 47px;
}

.main-page main {
  margin-top: -7px;
  width: 1126px;
  display: flex;
  flex-wrap: wrap;
}

.sidebar {
  width: 314px;
  height: 200px;
}

.sidebar-menu {
  margin-left: 99px;
  list-style-type: none;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
  -webkit-margin-before: 0px;
          margin-block-start: 0px;
  -webkit-margin-after: 0px;
          margin-block-end: 0px;
}

.sidebar-menu-item {
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  padding: 15px 0;
  letter-spacing: 0.666667px;
  cursor: pointer;
}

.sidebar-menu-item-active {
  font-weight: 500;
}

.sidebar-menu-item:first-child {
  padding-top: 0px;
}

.sort-btn {
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.7px;
  padding-top: 35px;
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}

.dropdownOff {
  width: 12px;
  height: 6px;
  position: absolute;
  right: 0;
  margin-right: 101px;
  background: center center url(/test_shop-page/static/media/dropdown-off.9ca489c0.svg);
}

.dropdownOn {
  transform: rotate(180deg);
}

.sidebar-dropdown {
  border: 1px solid black;
  margin: 0 62px 0 60px;
}

.sidebar-dropdown-disabled {
  display: none;
}

.dropdown-menu {
  list-style-type: none;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
  -webkit-margin-before: 0px;
          margin-block-start: 0px;
  -webkit-margin-after: 0px;
          margin-block-end: 0px;
  text-align: right;
  padding: 20px 15px;
}

.dropdown-menu-item {
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  text-align: right;
  letter-spacing: 0.6px;
  margin-bottom: 15px;
  cursor: pointer;
}

.dropdown-menu-item:last-child {
  margin-bottom: 0px;
}

.item-card {
  width: 280px;
  height: 506px;
  background-color: white;
  margin-right: 80px;
  margin-bottom: 30px;
  cursor: pointer;
}

.item-card:hover {
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.0697115);
}

.item-card-image {
  width: 280px;
  height: 389px;
  background-color: #a3a3a3;
}

.item-card-description {
  padding: 15px 20px 18px 20px;
}

.item-card-description-category {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 1px;
  color: #A3A3A3;
  text-transform: uppercase;
}

.item-card-description-name {
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  margin-top: 2px;
}

.item-card-description-price {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 1px;
  text-align: center;
  margin-top: 15px;
}

.item-card-description-stock {
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 1px;
  color: #A3A3A3;
  text-align: center;
}

.product-page {
  display: flex;
  margin-top: 32px;
}

.item-preview {
  height: 850px;
  width: 560px;
  background-color: rgb(179, 179, 179);
  margin-top: -112px;
}

.product-information {
  margin-left: 110px;
}

.back-btn {
  background-color: transparent;
  border: none;
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.666667px;
  text-transform: uppercase;
  cursor: pointer;
}

.back-btn:focus {
  outline: none;
}

.back-symbol {
  transform: rotate(90deg);
  margin-right: 8px;
}

.product-category {
  margin-top: 59px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 1px;
  color: #A3A3A3;;
  text-transform: uppercase;
}

.product-name {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 47px;
  margin-top: 4px;
}

.action-wrapper {
  margin-top: 30px;
}

.size-chooser {
  border: 2px solid #000000;
  box-sizing: border-box;
  display: inline-block;
  padding: 22px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.size-chooser img {
  margin-left: 11px;
}

.add-to-cart-btn {
  background-color: transparent;
  border: 2px solid #000000;
  box-sizing: border-box;
  padding: 22px 78px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #000000;
  margin-left: 20px;
  cursor: pointer;
}

.add-to-cart-btn-disabled {
  border: 2px solid #C4C4C4;
  color: #C4C4C4;
  cursor: not-allowed;
}

.add-to-cart-btn {
  outline: none;
}

.product-description-wrapper{
  display: flex;
}

.product-description {
  width: 357px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  margin-top: 60px;
}

.product-specification {
  margin-top: 65px;
  margin-left: 104px;
}

.product-specification-properties {
  display: inline-block;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #A3A3A3;
}

.product-specification-values {
  display: inline-block;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 1px;
  color: #000000;
  margin-left: 38px;
}

